<template>
  <div class="home-container">
    <van-field
        readonly
        clickable
        label="球队"
        :value="valueteam"
        placeholder="选择球队"
        input-align="right"
        @click="showPickerTeam = true"
        />
        <van-popup v-model="showPickerTeam" round position="bottom">
        <van-picker
            show-toolbar
            :columns="columns"
            @cancel="showPickerTeam = false"
            @confirm="onConfirmTeam"
        />
        </van-popup>

        <van-cell title="比赛时间段" :value="valuetime" @click="showPickerTime = true" />
        <van-button class="search" @click="onLoad" type="warning"  block round  size="small" :loading="loading">查询</van-button>

        <van-calendar v-model="showPickerTime" type="range" @confirm="onConfirmTime" :min-date="minDate" />

        

        <ItemView :items="items"></ItemView>
  </div>
</template>
<script>
import { selectListAPI } from '@/api'
import ItemView from '@/views/item'
export default {
  name:'HomeView',
  components: {ItemView},
  data() {
    return {
        valueteam: '',
        showPickerTeam: false,
        columns: ['76人','勇士','猛龙','雄鹿','热火','灰熊','骑士','爵士','开拓者','湖人','快船','马刺','掘金','火箭','太阳','凯尔特人','雷霆','鹈鹕','篮网','老鹰','森林狼','奇才','公牛','黄蜂','步行者','尼克斯','国王','魔术','活塞','独行侠'],
        valueStartTime: '',
        valueEndTime:'',
        showPickerTime: false,
        loading:false,
        minDate:new Date('2016-01-01'),
        items:{}
    }
  },
  computed: {
    valuetime(){
      return  this.valueStartTime+'-'+ this.valueEndTime
     
       
    }
  },
  watch: {
    
  },
  props: {},
  methods: {

    async onLoad(){
      this.loading = true
      const { data } = await selectListAPI({
        name:this.valueteam,
        startDate:this.valueStartTime,
        endDate:this.valueEndTime
      })
      console.log(data);
      this.items=data
      this.loading=false
    },

    onConfirmTeam(value) {
      this.valueteam = value;
      this.showPickerTeam = false;
    },
    onConfirmTime(date) {
        const [start, end] = date;
        this.showPickerTime = false;
        this.valueStartTime = this.formatDate(start)
        this.valueEndTime = this.formatDate(end)
    },
    formatDate(date) {
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
  },
  created() {},
}
</script>

<style scoped lang="less">
.home-container{
  .search{
    margin: auto;
    width: 80%;
  }
}
</style>
